import React from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
} from 'reactstrap';

export default class Menu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
    };
  }
  toggleMenu = () => {
    this.setState({
      isOpen: !this.state.isOpen,
    });
  };
  handleScrollTo = name => {
    if (window.innerWidth >= 768) {
      if (name === 'about') {
        window.scrollTo(0, 0);
      } else if (name === 'resume') {
        window.scrollTo(0, 470);
      } else if (name === 'skills') {
        window.scrollTo(0, 670);
      } else if (name === 'experience') {
        window.scrollTo(0, 1120);
      } else if (name === 'education') {
        window.scrollTo(0, 1630);
      } else if (name === 'certifications') {
        window.scrollTo(0, 2450);
      } else if (name === 'workedon') {
        window.scrollTo(0, document.body.scrollHeight);
      } else if (name === 'contact') {
        window.scrollTo(0, document.body.scrollHeight);
      }
    } else {
      if (name === 'about') {
        window.scrollTo(0, 0);
      } else if (name === 'resume') {
        window.scrollTo(0, 880);
      } else if (name === 'skills') {
        window.scrollTo(0, 1250);
      } else if (name === 'experience') {
        window.scrollTo(0, 1920);
      } else if (name === 'education') {
        window.scrollTo(0, 3000);
      } else if (name === 'certifications') {
        window.scrollTo(0, 4750);
      } else if (name === 'workedon') {
        window.scrollTo(0, document.body.scrollHeight);
      } else if (name === 'contact') {
        window.scrollTo(0, document.body.scrollHeight);
      }
      this.setState({
        isOpen: !this.state.isOpen,
      });
    }
  };
  render() {
    return (
      <div>
        <Navbar
          color="primary"
          dark
          expand="md"
          style={{ position: 'fixed', width: '100%', zIndex: '100', top: '0' }}
        >
          <NavbarBrand href="/">My Portfolio</NavbarBrand>
          <NavbarToggler onClick={this.toggleMenu} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem onClick={this.toggleMenu}>
                <NavLink
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    this.handleScrollTo('about');
                  }}
                >
                  ABOUT
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    this.handleScrollTo('resume');
                  }}
                >
                  RESUME
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    this.handleScrollTo('skills');
                  }}
                >
                  SKILLS
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    this.handleScrollTo('experience');
                  }}
                >
                  EXPERIENCE
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    this.handleScrollTo('education');
                  }}
                >
                  EDUCATION
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    this.handleScrollTo('certifications');
                  }}
                >
                  CERTIFICATIONS
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    this.handleScrollTo('workedon');
                  }}
                >
                  WORKED ON
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    this.handleScrollTo('contact');
                  }}
                >
                  CONTACT
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}
